___CSS_LOADER_EXPORT___.push([module.id, `* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #C36A2D;
  font-family: 'Poppins', sans-serif;
}

a {
  color: #E2C275;
  min-width: 44px;
  min-height: 44px;
}

a:hover,
a:active {
  color: #C36A2D;
}



body {
  width: 100%;
  background-color: #FBFBFB;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
}

.skip-link {
  position: absolute;
  text-decoration: none;
  top: -40px;
  left: 0;
  background-color: #C36A2D;
  color: #FBFBFB;
  padding: 8px;
  z-index: 999999;
}

.skip-link:hover {
  color: #EADCA6;
}


.skip-link:focus {
  top: 0;
