___CSS_LOADER_EXPORT___.push([module.id, `.detail{
  margin: 32px auto auto;
  text-align: left;
}

.detail h2,
.review h2 {
  margin-bottom: 0 !important;
}

.detail .title {
  font-size: 2.25rem;
  font-weight: 800;
}

.detail img {
  width: 100%;
  height: 400px;
  object-fit: cover;
}

.detail .info,
.detail .overview {
  margin-top: 16px;
}

.detail .info ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.detail .info ul li {
  margin-bottom: 8px;
}


.col {
  width: 100%;
  border-radius: 5px;
  overflow: visible;
}

.card {
  background-color: #FBFBFB;
  box-shadow:  0 4px 8px 0 rgba(0, 0, 0, 0.2);
  height: 100%;
  overflow: hidden;
  position: relative;
  border-radius: 4px;
}

.card .city-label {
  background-color: #C36A2D;
  margin: 1em;
  padding: .5em 1em;
  position: absolute;
  color: #FBFBFB;
  font-weight: 600;
  text-align: left;
}

.card img {
  width: 100%;
  max-height: 220px;
  object-fit: cover;
  z-index: 1;
}

.card-body {
  padding: 16px;
}

.card-title a {
  font-weight: 600;
  min-height: 44px;
  min-width: 44px;
  text-decoration: none;
  display: inline-block;
}

.card-text {
  color: #C36A2D;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

.card .rating {
  color: #C36A2D;
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: .5em;
}


