___CSS_LOADER_EXPORT___.push([module.id, `.detail {
    display: grid;
    grid-row-gap: 16px;
}

@media screen and (max-width: 600px) {
    nav {
        padding: 0 46px;
    }
    
    nav a:hover {
        background-color: transparent !important;
    }
    
    .hero .title {
        font-size: 2.3rem;
    }
  
    .hero .subtitle {
        font-size: 1.5rem;
    }
  
    .menu-list {
        background-color: #E2C275;
        fill-opacity: 0.5;
        position: fixed;
        top: 0;
        right: 0;
        width: 60%;
        height: 100%;
        margin-top: 0;
        padding: 64px 46px;
        flex-direction: column;
        align-items: flex-end;
        transform: translateX(100%);
        transition: transform 0.5s;
    }
  
    .menu-list li {
        padding: 10px 0;
    }
    
    .menu-list a {
        font-size: 1.5rem;
        color: #FBFBFB;
    }
    
    .open .menu-list {
        transform: translateX(0);
    }
  
    .menu-button {
        margin: auto 0;
        min-width: 44px;
        min-height: 44px;
        border: 0;
        border-radius: 3px;
        background-color: #FBFBFB;
        color: #E2C275;
        outline: none;
        font-size: 1.25rem;
        display: block;
        z-index: 2;
        cursor: pointer;
    }
    
    /* .menu i.fa-x,
    .menu.open i.fa-bars {
        display: none;
        animation: enter 0.8s;
    } */
  
    .menu.open i.fa-x,
    .menu i.fa-bars {
        display: block;
        animation: enter 0.8s;
    }
  
    .menu-list a:hover,
    .menu-list a:active {
        font-weight: 800;
        transition: ease-out;
        transition-duration: 0.2s;
        border-bottom: 2px solid #C36A2D;
        color: #C36A2D;
    }
  
    .col {
        width: 100%;
    }
  
    .detail, resto-review {
        width: 100%;
    }
}

@media screen and (min-width: 700px) {
    .detail {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 10px;
        grid-row-gap: 16px;
    }
}

@media screen and (min-width: 800px) {
    .menu-list {
        margin: 20px 0;
        font-size: 1.1rem;
    }
  
    .menu-list li {
        margin-left: 0;
    }
  
    .detail {
        width: 100%;
    }
}

@media screen and (min-width: 900px) {
    .detail {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
    }
}

@media screen and (min-width: 1200px) {
    main{
        max-width: 1200px;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles/_responsive.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,kBAAkB;AACtB;;AAEA;IACI;QACI,eAAe;IACnB;;IAEA;QACI,wCAAwC;IAC5C;;IAEA;QACI,iBAAiB;IACrB;;IAEA;QACI,iBAAiB;IACrB;;IAEA;QACI,yBAAyB;QACzB,iBAAiB;QACjB,eAAe;QACf,MAAM;QACN,QAAQ;QACR,UAAU;QACV,YAAY;QACZ,aAAa;QACb,kBAAkB;QAClB,sBAAsB;QACtB,qBAAqB;QACrB,2BAA2B;QAC3B,0BAA0B;IAC9B;;IAEA;QACI,eAAe;IACnB;;IAEA;QACI,iBAAiB;QACjB,cAAc;IAClB;;IAEA;QACI,wBAAwB;IAC5B;;IAEA;QACI,cAAc;QACd,eAAe;QACf,gBAAgB;QAChB,SAAS;QACT,kBAAkB;QAClB,yBAAyB;QACzB,cAAc;QACd,aAAa;QACb,kBAAkB;QAClB,cAAc;QACd,UAAU;QACV,eAAe;IACnB;;IAEA;;;;OAIG;;IAEH;;QAEI,cAAc;QACd,qBAAqB;IACzB;;IAEA;;QAEI,gBAAgB;QAChB,oBAAoB;QACpB,yBAAyB;QACzB,gCAAgC;QAChC,cAAc;IAClB;;IAEA;QACI,WAAW;IACf;;IAEA;QACI,WAAW;IACf;AACJ;;AAEA;IACI;QACI,aAAa;QACb,8BAA8B;QAC9B,qBAAqB;QACrB,kBAAkB;IACtB;AACJ;;AAEA;IACI;QACI,cAAc;QACd,iBAAiB;IACrB;;IAEA;QACI,cAAc;IAClB;;IAEA;QACI,WAAW;IACf;AACJ;;AAEA;IACI;QACI,aAAa;QACb,kCAAkC;IACtC;AACJ;;AAEA;IACI;QACI,iBAAiB;IACrB;AACJ","sourcesContent":[".detail {\r\n    display: grid;\r\n    grid-row-gap: 16px;\r\n}\r\n\r\n@media screen and (max-width: 600px) {\r\n    nav {\r\n        padding: 0 46px;\r\n    }\r\n    \r\n    nav a:hover {\r\n        background-color: transparent !important;\r\n    }\r\n    \r\n    .hero .title {\r\n        font-size: 2.3rem;\r\n    }\r\n  \r\n    .hero .subtitle {\r\n        font-size: 1.5rem;\r\n    }\r\n  \r\n    .menu-list {\r\n        background-color: #E2C275;\r\n        fill-opacity: 0.5;\r\n        position: fixed;\r\n        top: 0;\r\n        right: 0;\r\n        width: 60%;\r\n        height: 100%;\r\n        margin-top: 0;\r\n        padding: 64px 46px;\r\n        flex-direction: column;\r\n        align-items: flex-end;\r\n        transform: translateX(100%);\r\n        transition: transform 0.5s;\r\n    }\r\n  \r\n    .menu-list li {\r\n        padding: 10px 0;\r\n    }\r\n    \r\n    .menu-list a {\r\n        font-size: 1.5rem;\r\n        color: #